:root {
  --blue: #25476a;
  --gold: #f8ac1d;
}

* {
  font-family: 'Mada';
  background-color: var(--blue);
}

body {
  font-family: 'Mada', sans-serif;
}

.jumbotron {
  background-color: var(--blue);
  color: var(--gold);
  margin-bottom: 0;
}

h1 {
  font-family: 'Milonga', serif;
}

a {
  text-decoration: none;
}

.nav {
  background-color: var(--gold);
}

a.nav-link {
  color: var(--blue);
}

a.nav-link:hover {
  color: var(--blue);
}

.nav-item > a.nav-link {
  background-color: var(--gold);
  color: var(--blue);
  border-bottom-color: white;
}

.nav-item > a.nav-link.active {
  background-color: var(--blue);
  color: var(--gold);
  border-bottom-color: var(--blue);
}

.about {
  background-color: var(--blue);
  color: var(--gold);
}

.bio-heading {
  margin: 1rem 0 1rem 0;
}

.about > .about-text {
  margin: 0 3rem 0 3rem;
}

.profile {
  margin: 1rem 1rem 0 1rem;
}

.profile-pic {
  max-width: 100%;
}

.portfolio {
  background-color: var(--blue);
}

.portfolio > .card {
  max-width: 800px;
  margin-top: 1.5rem;
  border-color: var(--gold);
}

.card > .card-header {
  background-color: var(--gold);
  color: var(--blue);
}

.card-body > .card-text {
  color: var(--gold);
  padding-top: 1rem;
  padding-bottom: 1rem;
}

.card > .card-body > .btn {
  background-color: var(--gold);
  margin: 0 1rem 0 1rem;
}

.card > .card-body > .btn > a{
  color: var(--blue);
  text-decoration: none;
  font-weight: 600;
}

form {
  background-color: var(--blue);
  color: var(--gold);
  margin-top: 3rem;
}

.form > .btn {
  background-color: var(--gold);
  color: var(--blue);
  font-weight: 600;
}

.resume {
  background-color: var(--blue);
  padding-top: 2rem;
}

.resume-sheets {
  max-width: 75%;
}

.resume-img {
  max-width: 100%;
}

img.page-two {
  margin-top: 1rem;
}

h3 {
  color: var(--gold);
  text-decoration: underline;
  margin-bottom: 2rem;
}

.alert {
  border-radius: 5rem;
  margin-top: 2rem;
  font-size: 1.5rem;
  font-weight: 600;
}

.skills {
  margin-left: 1rem;
}

.skills > img {
  max-width: 6rem;
  margin-bottom: 1rem;
}

.footer {
  background-color: var(--blue);
  padding: 2.5rem 0 1.5rem 0;
}

.footer-img {
  max-width: 4rem;
  margin: 0 1.5rem 0 1.5rem;
}

.linkedin {
  max-height: 3.2rem;
}